 <!-- eslint-disable -->
<template>
  <b-card title="Agregar Inventario">
    <!-- Componente para mostrar errores -->
    <ErrorValidation :errors="validationErrors" v-if="validationErrors"></ErrorValidation>
    <div>
      <b-button
        variant="primary"
        class="mb-2"
        @click="$router.go(-1)">       
         Regresar 
      </b-button>
      <validation-observer 
        ref="form_inventory" 
        #default="{invalid}">
        <b-form
          ref="formInventory"
          :style="{height: trHeight}"
          class="repeater-form"
          @submit.prevent="repeateAgain"
        >

          <!-- Row Loop -->
          <b-row
            v-for="(item, index) in items"
            :id="item.id"
            :key="item.id"
            ref="row"
          >

            <!-- Code -->
            <b-col md="2">
              <b-form-group
                label="Código"
                label-for="code"
              >
              <validation-provider
                #default="{ errors }"
                name="código"
                rules="required|alpha-dash"
                >
                <b-form-input
                  id="code"
                  type="text"
                  v-model="item.code"
                  placeholder="Código"
                  :state="errors.length > 0 ? false:null"               
                />
                <small class="text-danger">{{ errors[0] }}</small>
              </validation-provider>
              </b-form-group>
            </b-col>

            <!-- Reference -->
            <b-col md="2">
              <b-form-group
                label="Referencia"
                label-for="reference"
              >
                <validation-provider
                  #default="{ errors }"
                  name="referencia"
                  rules="required"
                  >
                  <b-form-input
                    id="reference"
                    type="text"
                    v-model="item.reference"
                    placeholder="Referencia"
                    :state="errors.length > 0 ? false:null"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
            </b-col>

            <!-- Line -->
            <b-col md="2">
              <b-form-group
                label="Linea"
                label-for="line"
                >
                <validation-provider
                  #default="{ errors }"
                  name="linea"
                  rules="required"
                  >
                  <b-form-input
                    id="line"
                    type="text"
                    v-model="item.line"
                    placeholder="Linea Producto"
                    :state="errors.length > 0 ? false:null"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
            </b-col>

            <!-- Cost -->
            <b-col
              lg="2"
              md="2"
            >
              <b-form-group
                label="Costo"
                label-for="cost"
                >
                <validation-provider
                  #default="{ errors }"
                  name="costo"
                  rules="required|numeric"
                  >
                  <b-form-input
                    id="cost"
                    type="text"
                    v-model="item.cost"
                    placeholder="Costo"
                    :state="errors.length > 0 ? false:null"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
            </b-col>

            <!-- Stock -->
            <b-col md="1">
              <b-form-group
                label="Stock"
                label-for="stock"
                >
               <validation-provider
                  #default="{ errors }"
                  name="stock"
                  rules="required|numeric"
                  >
                  <b-form-input
                    id="stock"
                    type="text"
                    v-model="item.stock"
                    :state="errors.length > 0 ? false:null"
                  />
                 <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
            </b-col>

            <!-- Min Stock -->
            <b-col md="1">
              <b-form-group
                label="MStock"
                label-for="min_stock"
                >
                <validation-provider
                    #default="{ errors }"
                    name="min_stock"
                    rules="required|numeric"
                    >
                  <b-form-input
                    id="min_stock"
                    type="text"
                    v-model="item.min_stock"
                    :state="errors.length > 0 ? false:null"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
            </b-col>

            <!-- Remove Button -->
            <b-col
              lg="2"
              md="3"
              class="mb-50"
            >
              <b-button
                v-ripple.400="'rgba(234, 84, 85, 0.15)'"
                variant="outline-danger"
                class="mt-0 mt-md-2"
                @click="removeItem(index)"
              >
                <feather-icon
                  icon="XIcon"
                  class="mr-25"
                />
                <span>Eliminar</span>
              </b-button>
            </b-col>
            <b-col cols="12">
              <hr>
            </b-col>
          </b-row>

          <!-- Boton Agregar Campos -->
          <b-button
            v-ripple.400="'rgba(255, 255, 255, 0.15)'"
            class="mr-2 my-1"
            variant="secondary"
            @click="repeateAgain"
          >
            <feather-icon
              icon="PlusIcon"
              class="mr-25"
            />
            <span>Agregar Campos</span>
          </b-button>  

          <!-- Boton Registrar -->
          <b-button
            class="my-1"
            variant="success"
            :disabled="invalid"
            @click="addInventory"
          >
            <feather-icon
              icon="CheckIcon"
              class="mr-25"
            />
            <span>Registrar Inventario</span>
          </b-button>

        </b-form>       
      </validation-observer>
    </div>     
  </b-card>
</template>

<script>

/*eslint-disable */

import {
  BForm, BFormGroup, BFormInput, BRow, BCol, BButton, BCard,
} from 'bootstrap-vue'
import { heightTransition } from '@core/mixins/ui/transition'
import Ripple from 'vue-ripple-directive'

// Servicios
import * as inventoryService from '@/services/store/store'

// Notificaciones
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

export default {
  components: {
    BForm,
    BRow,
    BCol,
    BButton,
    BFormGroup,
    BFormInput,
    BCard,
  },
  directives: {
    Ripple,
  },
  mixins: [heightTransition],
  data() {
    return {
      validationErrors: '',
      items: [{
        id: 1,
        code : '',
        reference : '',
        line : '',
        cost : '',
        stock : '',
        min_stock : '',
      }],
      nextInventoryId: 1,
    }
  },

  mounted() {
    this.initTrHeight()
  },

  created() {
    window.addEventListener('resize', this.initTrHeight)
  },

  destroyed() {
    window.removeEventListener('resize', this.initTrHeight)
  },

  methods: {

    repeateAgain() {
      this.items.push({
        id: this.nextInventoryId += this.nextInventoryId,
      })

      this.$nextTick(() => {
        this.trAddHeight(this.$refs.row[0].offsetHeight)
      })
    },

    removeItem(index) {
      this.items.splice(index, 1)
      this.trTrimHeight(this.$refs.row[0].offsetHeight)
    },

    initTrHeight() {
      this.trSetHeight(null)
      this.$nextTick(() => {
        this.trSetHeight(this.$refs.form_inventory.scrollHeight)
      })
    },

    async addInventory() {
      try {

        // Valida si el formulario es correcto
        const isValid = await this.$refs.form_inventory.validate();

        if(!isValid) {
          this.showToast('Alerta', 'AlertTriangleIcon', 'Validar el formulario', 'warning') 
        }else {
          const response = await inventoryService.addInventory(this.items) 

          if(response.status === 200) {
            this.validationErrors = ''
            this.$refs.formInventory.reset()
            this.showToast('Agregado', 'CheckIcon', 'Inventario Agregado', 'success')
            
            setTimeout(() => {
              this.$router.go(-1)
            }, 3000)
          }
        }
      } catch (error) {
          if (error.response.status === 422){
            this.validationErrors = error.response.data.errors;
          }          
      }
    },

    showToast(title, icon, text, variant) {
      this.$toast({
        component: ToastificationContent,
        props: {
          title,
          icon,
          text,
          variant,
        },
      })
    },

  },
}
</script>

<style lang="scss" scoped>
.repeater-form {
  overflow: hidden;
  transition: .35s height;
}
</style>
